@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.banner {
  background: rgb(53,134,119);
  font-family: "Lexend";
  background: linear-gradient(80deg, rgba(53,134,119,1) 0%, rgba(119,160,176,1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.textBox p{
  margin-top: -2vh;
}

#queryInput{
  width: 50vw;
  height: 2vh;
  border: 3px white solid;
  font-family: "Lexend";
  border-radius: 50px;
  padding: 2vh 2vw;
  font-size: 16px;
}

#searchButton{
  background-color: white;
  font-family: "Lexend";
  border-radius: 50px;
  border: 3px solid white;
  margin-top: 3vh;
  padding: 2vh;
  width: 10vw;
  color: #358677;
  font-size: 20px;
  transition: 0.4s;
}
#searchButton:hover{
  background-color: #cccccc;
  border: 3px solid #cccccc;
}

#searchButton:active{
  background-color: #358677;
  border: 3px solid #358677;
  color: white;
}

@media screen and (max-width: 1000px){
  #queryInput{
    width: 80vw;
  }
  #searchButton{
    width: 40vw;
    padding: 10px;
  }
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#results{
    align-items: center;
    font-family: Lexend;
    padding: 20px;
}

.container{
    border-radius: 20px;
    padding: 20px;
    margin: 1vw 1vh;
    width: 60vw;
    height: 10vh;
    margin: 0 auto;
    display: inline-block;
    transition: 0.3s;
    color: #575757;
}


.NIContainer{
    text-align: left;
    float: left;
    max-width: 50vw;
    display: inline-block;
    padding-bottom: 10vh; 
    margin-top: -3vh;
}

.NIContainer p{
    margin-top: -2vh;
    font-style: italic;
}

.NIMContainer{
    float: right;
    display: inline-block;
    margin-top: -2vh;
}

.container:hover{
    color: rgb(53,134,119);
    box-shadow: 7px 10px 36px -10px rgba(53,134,119,0.59);
}



@media screen and (max-width: 1000px){

    #results{
        padding: 5vh 0;
    }

    .NIContainer{
        margin-top: -1.5vh;
    }
    .container{
        width: 90vw;
    }

    .container h2{
        font-size: 20px;
        min-width: 95vw;
    }
}
